a {
  overflow: hidden;
  position: relative;
  display: inline-block;
  text-decoration: none; }

a::before {}
a::after {
    content: '';
    position: absolute;
    width: 100%;
    left: 0; }

a::before {
  background-color: rgb(235, 206, 200);
  height: 2px;
  bottom: 0;
  transform-origin: 100% 50%;
  transform: scaleX(0);
  transition: transform .3s cubic-bezier(0.76, 0, 0.24, 1); }

a::after {
  content: attr(data-replace);
  height: 100%;
  top: 0;
  transform-origin: 100% 50%;
  transform: translate3d(200%, 0, 0);
  transition: transform .3s cubic-bezier(0.76, 0, 0.24, 1);
  color: rgb(245, 235, 233); }

a:hover::before {
  transform-origin: 0% 50%;
  transform: scaleX(1); }

a:hover::after {
  transform: translate3d(0, 0, 0); }

a span {
    display: inline-block;
    transition: transform .3s cubic-bezier(0.76, 0, 0.24, 1); }

a:hover span {
  transform: translate3d(-200%, 0, 0); }

